%flex-row {
  display: flex;
  flex-direction: row;
}
%flex-col {
  display: flex;
  flex-direction: column;
}
body {
  font-size: 16px;
}
.content {
  padding: 0 15%;
}
.btn {
  background-color: #1e88e5;
  border: 2px solid #1e88e5;
  color: #fff;
  font-size: 1rem;
  padding: 0.2rem 1rem;
  &:hover {
    background-color: #fff;
    color: #1e88e5;
  }
}
