header {
  background-color: #fafafa;
  position: sticky;
  width: 100%;
}
.header-contats {
  background-color: #1e88e5;
  padding: 0 15%;
  // .header-contats__elem

  &__elem {
    @extend %flex-row;
    justify-content: flex-end;
    gap: 1rem;
    padding: 0.5rem;
    & a {
      & span {
        color: #fff;
        font-weight: 900;
      }
    }
  }
}
.header-contacts-elem-img {
}
.header-nav {
  @extend %flex-row;
  justify-content: space-between;
  padding: 0.7rem 15%;
  // .header-nav__left
  &__left {
    @extend %flex-row;
    gap: 2rem;
  }
}
.header-title {
  text-transform: uppercase;
}
.navbar {
  @extend %flex-row;
  gap: 1rem;
  align-items: center;

  & li {
    list-style-type: none;
    & a {
      color: #000;
      font-size: 0.9rem;
      text-transform: uppercase;
    }
  }
}
.header-popup-btn {
  @extend .btn;
  right: 0;
}
